* {
  font-family: "Segoe UI", "Frutiger", "Frutiger Linotype", "Dejavu Sans",
    "Helvetica Neue", "Arial", "sans-serif";
  margin: 0;
  padding: 0;
}

.App {
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
}
.spaceFilling {
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

.row {
  display: flex;
  flex-direction: row;
}
